import React, { useMemo } from 'react';

import './index.scss';

type DescriptionGridItem = {
  title: string;
  src?: string;
  descriptions: string[];
};

type DescriptionGridTypes = {
  column?: number;
  titleWidth?: string;
  borderRadius?: string;
  boxShadow?: string;
  itemHeight: string;
  items: Array<DescriptionGridItem>;
};

const DescriptionGrid: React.FC<DescriptionGridTypes> = ({
  column,
  itemHeight,
  borderRadius,
  boxShadow,
  items,
  titleWidth = '200px'
}) => {
  const itemWidth = useMemo(() => `${(1 / (column ?? 3)) * 100}%`, [column]);

  return (
    <div className="description-area">
      {items.map(({ title, src, descriptions }, index) => (
        <div
          className="description-item"
          key={index}
          style={{ 
            width: `calc(${itemWidth} - 45px)`,
            height: itemHeight,
            borderRadius,
            boxShadow,
            marginBottom: 45,
            textAlign: 'center'
          }}
        >
          {src ? (
            <img
              src={src}
              style={{
                marginBottom: '20px'
              }}
            />
          ) : (
            ''
          )}
          {/* <img src={src} alt="" /> */}
          <h6 style={{ width: titleWidth }} className="title">
            {title}
          </h6>
          {descriptions.map((desc, index) => (
            <p className="description" key={index}>
              {desc}
            </p>
          ))}
        </div>
      ))}
    </div>
  );
};

export default React.memo(DescriptionGrid);
